import React, { useState, useEffect } from 'react'

const TicTacToe = () => {
  const emptyBoard = Array(9).fill('');
  const [ board, setBoard ] = useState(emptyBoard);
  const [ currentPlayer, setCurrentPlayer ] = useState("O");
  const [winner, setWinner] = useState(null);
  
const handleCellClic = index => {
  if (winner) {
    window.alert('Jogo finalizado!')
    return null
  };
  if (board[index] !== "") return window.alert('Posição ocupada');
  setBoard(board.map((item, itemIndex) => 
  itemIndex === index ? currentPlayer : item
  ));
  setCurrentPlayer(currentPlayer === 'X' ? 'O' : 'X')
}

const checkDrow = () => {
  if (board.every(item => item !== "") && winner === null) setWinner('D')
}

const checkWinner = () => {
  const possibleWayToWin = [
    [board[0], board[1], board[2]],
    [board[3], board[4], board[5]],
    [board[6], board[7], board[8]],

    [board[0], board[3], board[6]],
    [board[1], board[4], board[7]],
    [board[2], board[5], board[8]],

    [board[0], board[4], board[8]],
    [board[2], board[4], board[6]],
  ]

  possibleWayToWin.forEach(cells => {
    if (cells.every(cell => cell === 'O')) {
      setWinner('O')
      window.alert('O venceu!');
    };
    if (cells.every(cell => cell === 'X')) {
      setWinner('X')
      window.alert('X venceu!')
    };
  });
}

useEffect(checkWinner, [board])
checkDrow();

const resetGame = () => {
  setCurrentPlayer('O');
  setBoard(emptyBoard);
  setWinner(null);
}

  return (
    <main>
      <h1 className="title">Tic Tac Toe</h1>
      <div
        className={`board ${winner? 'game-over' : ''}`}
        data-testid="board"
      >
        {board.map((item, index) => (
          <div
            key={index}
            className={`cell ${item}`}
            onClick={() => handleCellClic(index)}
            data-testid="cell"
          >
            {item}
          </div>
        ))}
      </div>
      <button data-testid="submit" onClick={resetGame}>Restart</button>
      {winner &&
        <footer data-testid="footer">
          {winner === 'D'
          ? <h2 className="winner-message">
            <span className={winner}>EMPATOU!!!</span>
          </h2>
          : <h2 className="winner-message">
          <span className={winner}>{winner} WON!!!</span>
          </h2>
          }
        </footer>
      }
    </main>
  )
}

export default TicTacToe
